import { POST, RequestParams, User, GET_FILE_URL } from "../utils/common";
export default {
  data() {
    return {
      GET_FILE_URL: GET_FILE_URL,
      user: User.info,
      errMsg:null,
      cars: [],
    };
  },
  mounted: function() {
    this.getCars();
  },
  methods: {
    getCars: function() {
      POST("/mobile/getcarlst", new RequestParams().addDataItemAttr(0, "household_id", this.user.house_id))
        .then((res)=>{
            this.cars = res.dataItems.map(o=>o.attributes);
        })
        .catch(({message}) =>{ this.$$alert(message)});
    }
  }
};



