import { POST, RequestParams, User, GET_FILE_URL } from "../utils/common";
export default {
  data() {
    return {
      user: User.info,
      parkingList: [],
      parkingBeforeProject: []
    };
  },
  methods: {
    getparkrecord: function() {
      POST("/mobile/getparkrecord", new RequestParams().addAttribute("house_id", this.user.house_id))
        .then((res)=> {
          this.parkingList = res.dataItems.map(o=>o.attributes);
        })
        .catch(({message}) =>{ this.$$alert(message)});
    }
  },
  mounted() {
    this.getparkrecord();
  }
};



