import { POST, RequestParams, User, GET_FILE_URL } from "../utils/common";
export default {
  data() {
    return {
      GET_FILE_URL: GET_FILE_URL,
      user: User.info,
      errMsg:null,
      carNos: [],
      items: [],
      args: {}
    };
  },
  mounted: function() {
    this.delayGetlist();
  },
  methods: {
    callbacks: function() {},
    delayGetlist: function() {
      POST("/delay/getlist")
        .then(res => {
          this.items = res.dataItems;
        })
        .catch(({message}) =>{ this.$$alert(message)});
    }
  }
};



