import Vue from 'vue'
import Router from 'vue-router'

import login from '../template/login'
import userCenter from '../template/userCenter'
import myData from '../template/myData'
import tempParking from '../template/tempParking'
import tempParking_temp from '../template/tempParking_temp'
import monthCard from '../template/monthCard'
import monthCard_renew from '../template/monthCard_renew'
import renew_succeed from '../template/renew_succeed'
import myCar from '../template/myCar'
import myCar_handle from '../template/myCar_handle'
import parkingRecord from '../template/parkingRecord'

Vue.use(Router)



export default new Router({
  routes: [
    {path: '/', component: userCenter, meta: {title: '个人中心', requireAuth:true}},
    {path: '/login', component: login, meta: {title: '登陆', requireAuth:false}},
    {path: '/myData', component: myData, meta: {title: '我的资料', requireAuth:true}},
    {path: '/tempParking', component: tempParking, meta: {title: '停车场后台管理系统', requireAuth:true}},
    {path: '/tempParking_temp', component: tempParking_temp, meta: {title: '停车场后台管理系统', requireAuth:true}},
    {path: '/monthCard', component: monthCard, meta: {title: '停车场后台管理系统', requireAuth:true}},
    {path: '/monthCard_renew', component: monthCard_renew, meta: {title: '停车场后台管理系统', requireAuth:true}},
    {path: '/renew_succeed', component: renew_succeed, meta: {title: '停车场后台管理系统', requireAuth:true}},
    {path: '/myCar', component: myCar, meta: {title: '停车场后台管理系统', requireAuth:true}},
    {path: '/myCar_handle', component: myCar_handle, meta: {title: '停车场后台管理系统', requireAuth:true}},
    {path: '/parkingRecord', component: parkingRecord, meta: {title: '停车场后台管理系统', requireAuth:true}}
  ]
})



