import { POST, RequestParams, User } from "../utils/common";
export default {
  data() {
    return {
      tel: "15013478559",
      code: "",
      timeId: null,
      time: 0,
      timeStr: "获取验证码"
    };
  },

  mounted() {},

  methods: {
    getSmsCode: function() {
      if (this.time > 0) {
        return;
      }
      if (this.tel.length != 11) {
        return this.$$alert("请输入正确的手机号码")
      }

      POST("/mobile/getsms", new RequestParams().addAttribute("tel", this.tel))
        .then(res => {
          this.time = 60;
          this.timeId = setInterval(()=> {
            this.timeStr = `(${--this.time})秒`;
            if (this.time <= 0) {
              this.timeStr = "获取验证码";
              clearInterval(this.timeId);
            }
          }, 1000);
        })
        .catch(({message}) =>{ this.$$alert(message)});
    },

    login: function() {
      POST("/mobile/login", new RequestParams()
      .addAttribute("tel", this.tel)
      .addAttribute("identifying_code", this.code))
        .then((res)=> {
          User.token = res.attributes.token
          User.infos = res.dataItems
          this.$router.push(this.$route.query.redirect || "/");
        })
        .catch(({message}) =>{ this.$$alert(message)});
    }
  }
};



