import Vue from "vue";
import {User, url} from "./utils/common"
export default {
  data() {
    return {
      errMsg: null,
      user:User
    };
  },
  name: "App",
  comments: {},
  mounted() {
    Vue.prototype.$$alert = message => (this.errMsg = message);
    if(this.$route.query.openId){
      User.openid = this.$route.query.openId;
    }
    if(!User.openid){
      location.href = url + "/wx/getOpenId?callbackUrl=" + location.href;
    }
    if (!/MicroMessenger|AlipayClient/.test(window.navigator.userAgent)) {
      this.errMsg = "请在微信或者支付宝APP中打开此页面";
    }
  }
};



