import licencePlate from "../components/licencePlate";
import { POST, RequestParams, User, GET_FILE_URL } from "../utils/common";

export default {
  data() {
    return {
      GET_FILE_URL: GET_FILE_URL,
      user: User.info,
      errMsg:null,
      carNos: []
    };
  },
  name: "box",
  components: {
    licencePlate
  },
  methods: {
    getfixedrecord: function() {
      POST("/mobile/getfixedrecord", new RequestParams().addAttribute("house_id", this.user.house_id))
      .then((res)=> {
        this.carNos = res.dataItems.map(o=>o.attributes);
      })
      .catch(({message}) =>{ this.$$alert(message)});
    },

    tmpholdfixed: function(car_no, startTime, endTime) {
      POST("/mobile/tmpholdfixed", new RequestParams()
      .addAttribute("household_id", this.user.house_id)
      .addAttribute("car_no", car_no)
      .addAttribute("begin_time", startTime)
      .addAttribute("end_time", endTime))
      .then((res)=> {
        this.getfixedrecord()
      })
      .catch(({message}) =>{ this.$$alert(message)});
    }
  },
  mounted() {
    this.getfixedrecord();
  }
};



