import moment from 'moment'
import {POST} from "../utils/common"
export default {
  data () {
    return {
      price: 250.0,
      num: 0,
      start: moment().format("YYYY-MM-DD"),
      end: moment().format("YYYY-MM-DD"),

      pay_mode: '1',
    }
  },
  mounted () {
  },
  methods: {
    addNum: function () {
      this.num += 1
      this.end = moment().add(this.num, 'months').format("YYYY-MM-DD")
    },
    minusNum: function () {
      this.num -= 1
      if (this.num <= 0) {
        this.num = 0;
      }
      this.end = moment().add(this.num, 'months').format("YYYY-MM-DD");
    },
    submit: function () {
      const that = this;
      POST('/delay/charge', {
        "attributes": {},
        "dataItems": [{
          "attributes": {
            car_auth_id: '',
            project_id: '',
            car_id: '',
            amount: this.num * this.price,
            pay_mode: this.pay_mode, //0：现金1：微信公众号支付2：微信刷卡支付3：支付宝网页支付4：支付宝刷卡支付
            old_end_time: this.start,
            new_end_time: this.end,
            source: 'app', //cloud(平台)、app(移动端)
            operator_id: '',
          },
          "objectId": "string",
          "subItems": [{}]
        }],
        "seqId": "string",
        "serviceId": "delay.charge"
      }).then(function (res) {
        if (res.resultCode !== 0) {
          that.$router.push({
            path: '/renew_succeed'
          });
        } else {
          that.$router.push({
            path: '/renew_succeed'
          });
        }
      }).catch(function (error) {
        console.log(error);
      });
    },
  }
}



