export const GET_FILE_URL = "http://119.29.152.124:8840/file/viewFile?relativePath=";


export const RequestDataItem = class RequestDataItem {

  constructor(obj = {}) {
    this.attributes = {};
    this.objectId = "";
    this.subItems = [];

    this.addAttribute("project_id", User.info.project_id);
    this.addAttribute("user_id", User.info.id);
    this.addAttribute("operator_id", User.info.id);
    $.extend(this, obj);
  }

  addAttributes(objs) {
    for (let key in objs) {
      this.addAttribute(key, objs[key]);
    }
    return this;
  }

  addAttribute(name, value) {
    if (!!value || typeof value == "number") {
      this.attributes[String(name).trim()] = String(value).trim();
    }
    else delete this.attributes[String(name).trim()];
    return this;
  }

  addSubItem(obj) {
    this.subItems.push(obj);
    return this;
  }

  addSubItems(objs) {
    for (let item of objs) {
      this.addSubItem(item);
    }
    return this;
  }
}

export const RequestParams = class RequestParams {

  constructor(obj = {}) {
    /**附带属性 */
    this.attributes = {};
    this.seqId = "";
    /**业务规则ID，按业务规范命名进行设置； */
    this.serviceId = "";
    /**业务对象列表； */
    this.dataItems = [];

    this.addAttribute("project_id", User.info.project_id);
    this.addAttribute("operator_id", User.info.id);
    this.addAttribute("user_id", User.info.id);

    $.extend(this, obj);
  }

  setServiceId(v){
    this.serviceId = v;
    return this;
  }

  getJsonParams() {
    if (User.isSystemAdmin) {
      let asp = this.serviceId.split(".")[1];
      if (asp == "getlist") {
        delete this.attributes["project_id"];
        for (let item of this.dataItems) {
          delete item["project_id"];
        }
      }
    }
    return this;
  }

  addAttributes(objs) {
    for (let key in objs) {
      this.addAttribute(key, objs[key]);
    }
    return this;
  }

  addAttribute(name, value) {
    if (!!value || typeof value == "number") {
      this.attributes[String(name).trim()] = String(value).trim();
    }
    else delete this.attributes[String(name).trim()];
    return this;
  }

  addDataItem(requestDataItem) {
    this.dataItems.push(requestDataItem);
    return this;
  }

  addDataItems(requestDataItems) {
    this.dataItems = this.dataItems.concat(requestDataItems);
    return this;
  }

  addDataItemAttr(index, key, value) {
    let dataItem = this.dataItems[index];
    if (dataItem) {
      dataItem.addAttribute(key, value);
    }
    else {
      dataItem = new RequestDataItem();
      dataItem.addAttribute(key, value);
      this.dataItems.push(dataItem)
    }
    return this;
  }

}

export const ResponseBody = class ResponseBody {

  constructor(obj = {}, code = 1) {

    /**附加属性； */
    this.attributes = {};
    /**返回的业务对象列表； */
    this.dataItems = [];
    /**resultCode对应的消息描述 */
    this.message = "";
    /**0：成功 1：失败  */
    this.resultCode = code || 0;
    this.seqId = "";
    /**业务规则ID，按业务规范命名进行设置； */
    this.erviceId = "";
    if(typeof(obj) == 'string'){
      this.message = obj;
      this.resultCode = code;
    }
    else $.extend(this, obj);
  }
}


/**用户数据管理 */
export const User = new class User {

  constructor() {
    this.__infos = JSON.parse(localStorage.getItem("userInfo") || "[]");
    this.__current_index = 0;
    this.__token = JSON.parse(localStorage.getItem("_token") || "{}");
    this.__openid = localStorage.getItem("__openid");
  }

  set openid(v){
    localStorage.setItem("__openid", this.__openid = v)
  }

  get openid(){
    return this.__openid;
  }

  set current_info(v){
    this.__current_index = v;
  }

  get token(){
    return this.__token || {};
  }

  set token(v){
    
    v.expires_in = new Date().getTime() + (v.expires_in*1000)
    console.log(new Date(v.expires_in))
    localStorage.setItem("_token", JSON.stringify(this.__token = v));
  }

  get project_id() {
    return this.isSystemAdmin ? null : this.info.project_id;
  }

  get isSystemAdmin() {
    return 3 == this.info.user_type;
  }

  get info() {
    return this.__infos[this.__current_index] || {};
  }

  set infos(vs) {
    for(let v of vs){
      v.attributes.login_time = moment().format("YYYY-MM-DD HH:mm:ss")
      for(let _v of v.subItems){
        delete _v.id;
        this.__infos.push($.extend(_v.attributes, v.attributes))
      }
    }
    localStorage.setItem("userInfo", JSON.stringify(this.__infos));
  }

};

export function orderPay(config) {
  return new Promise((resolve, reject)=>{
    if (/MicroMessenger/.test(window.navigator.userAgent)) { 
      function onBridgeReady(){
        WeixinJSBridge.invoke(
          'getBrandWCPayRequest', config,
          // {
          //     "appId":"wx2421b1c4370ec43b",     //公众号名称，由商户传入     
          //     "timeStamp":"1395712654",         //时间戳，自1970年以来的秒数     
          //     "nonceStr":"e61463f8efa94090b1f366cccfbbb444", //随机串     
          //     "package":"prepay_id=u802345jgfjsdfgsdg888",     
          //     "signType":"MD5",         //微信签名方式：     
          //     "paySign":"70EA570631E4BB79628FBCA90534C63FF7FADD89" //微信签名 
          // },
          function(res){
          if(res.err_msg == "get_brand_wcpay_request:ok" ){
          // 使用以上方式判断前端返回,微信团队郑重提示：
                //res.err_msg将在用户支付成功后返回ok，但并不保证它绝对可靠。
                resolve(res);
          }
          else {
            reject(res);
          }
        }); 
      }
      if (typeof WeixinJSBridge == "undefined"){
        if( document.addEventListener ){
            document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
        }else if (document.attachEvent){
            document.attachEvent('WeixinJSBridgeReady', onBridgeReady); 
            document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
        }
      }else{
        onBridgeReady();
      }
    } 
    else if (/AlipayClient/.test(window.navigator.userAgent)) { 
      AlipayJSBridge.call("tradePay", { tradeNO:config.orderId}, (data)=> {
        //支付成功后data.resultCode是900
        if ("9000" == data.resultCode || 8000 == data.resultCode) {
          //查询商户后台支付结果
          resolve(data);
        }
        else {
          reject(data);
        }
      });
    } 
    else {
      reject("仅支持支付宝支付和微信支付");
    }
  });
}


